.btn {
  width: 43px;
  // width가 주어진 상태에서 좌우 중앙 정렬
  margin-left: 4px;
  margin-right: 4px;
  margin-top: 0.7rem;
  border-radius: 4px;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  background: #22b8cf;
  color: white;
  height: 1.6rem;
  font-size: 1.2rem;

  .app-title {
    background: #22b8cf;
    color: white;
    height: 4rem;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
